.flex-item {
  display: flex;
  align-items: center; /* Centra verticalmente */
  gap: 8px;            /* Espacio entre el ícono y el texto (puedes ajustar) */
  text-decoration: none;
}

.icono {
  font-size: 20px;     /* Ajusta el tamaño del ícono si deseas */
}

.texto {
  font-size: 16px;     /* Ajusta el tamaño del texto si deseas */
  color: inherit;      /* Mantiene el color de texto si ya tienes estilos */
}
